var that = this;
export default {
	data() {
		return {
			info: [],
			range: 0,
		}
	},
	onLoad() {
		// 页面加载获取后台地址数据
		this.getAddressInfo()
	},
	methods: {
		// 获取地址信息
		getAddressInfo() {
			this.test.post('user/getAddrList').then(res => {
				console.log(res)
				if (res.statusCode == 200 && res.data.errorCode == 0) {
					this.info = res.data.value
				}else if(res.data.errorCode == 10001 || res.data.errorCode == 10002 || res.data.errorCode == 10003){
					uni.showModal({
						title: '提示',
						content: res.data.message,
						success: function(res) {
							if (res.confirm) {
								uni.reLaunch({
									url: '../login/login'
								})
							} else {
								uni.reLaunch({
									url: '../login/login'
								})
							}
						}
					})
				}else{
					uni.showToast({
						title: res.data.message,
						icon: "none"
					})
				}
			}).catch(err => {
				console.log(err)
			})
		},
		// 确认默认地址
		sure(e) {
			console.log(e)
			uni.showModal({
				"title": "提示",
				"content": '确认选择该地址为默认地址？',
				success: res => {
					if (res.confirm) {
						for (let i = 0; i < this.info.length; i++) {
							console.log(res)
							if (this.info[i].is_default == e.target.value) {
								this.range = i;
								console.log(this.info[i].cl_id)
								this.test.post("user/setDefaultAddr", {
									 addr_id:this.info[i].cl_id
								}).then(res=>{
									console.log(res)
									if(res.statusCode == 200 && res.data.errorCode == 0){
										uni.showToast({
											title:'设置成功'
										})
									}
								})
								break;
							}
						}
						// 点击取消
					}else if(res.cancel){
						this.info = '',
						this.getAddressInfo()
					}
				}
			})
		},
		newadd() {
			uni.navigateTo({
				url: 'newAddress/newAddress'
			})
		},
		edit() {},
		delate() {}
	},
}
